import server from '../../server.js';
export default {
	data() {
		return {
			xcx:true,
			userInfo:[],
			phoneifo:"",
			disabled:true,
			reg:false,
			modalName: null,
			user:"",
			password:"",
			timer:"",
			num:58,
			codeTitle:"获取验证码",
			code:true
			
		}
	},
	onLoad() {
		uni.getSystemInfo({
		    success: function (res) {
				var systemjson={
					phonebrand:res.brand+res.model,
					phonesystem:res.system
				}
				server.system=JSON.stringify(systemjson);
				console.log(server.system)
		    }
		});
	},
	methods: {
		getpage:function(id){
			if(id==1){
				uni.navigateTo({
					url:"../edit/edit?type=4"
				})
			}
			if(id==2){
				uni.navigateTo({
					url:"../edit/edit?type=6"
				})
			}
			if(id==3){
				uni.navigateTo({
					url:"../edit/edit?type=5"
				})
			}
			
		},
		getWechatOauth:function(){
			uni.getProvider({
				service: 'oauth',
				success: function (res) {
					console.log(res.provider)
					if (~res.provider.indexOf('weixin')) {
						uni.login({
							provider: 'weixin',
							success: function (loginRes) {
								console.log(loginRes)
								console.log(loginRes.authResult.unionid)
								
								uni.request({
									method:'POST',
									url: 'https://api.angeli.top/reg.php?type=appwxlogin', 
									data: {
										unionid: loginRes.authResult.unionid,
									},
									header: {
										'content-type': 'application/x-www-form-urlencoded'
									},
									success: (res) => {
										
										console.log(res);
										if(res.data.code=="0"){
											server.usersk=res.data.data.session_key
											uni.showToast({
												title: "你的微信还没有注册，请注册先",
												position:'bottom',
												icon:'none'
											});
											return;
											
										}else if(res.data.code=="1" ||res.data.code=="2"){
											server.userinfo=res.data.data;
											server.cookie=res.header['Set-Cookie'];
											console.log("记录cookie：",server.cookie)
											if(res.data.code=="2"){
												uni.showToast({
													title: res.data.msg,
													position:'bottom',
													icon:'none'
												})
											}else{
												if(server.userinfo.VIPEndTime>0){
													uni.showToast({
														title: '欢迎VIP：'+server.userinfo.UserName,
														position:'bottom',
														icon:'none'
													})
												}else{
													uni.showToast({
														title: '欢迎你,'+server.userinfo.UserName,
														position:'bottom',
														icon:'none'
													})
												}
											}
											
											setTimeout(function () {
												uni.redirectTo({
													url: '../Home/Home'
												})
											}, 2000);
										}
									},
									complete: () => {
										this.getPostData('new',0);
									},
									fail:(src) =>{
										uni.showToast({
											title: "未知原因，登录失败！",
											position:'bottom',
											icon:'none'
										})
										this.getPostData('new',0);
									},
								});
							}
						});
					}
				}
			})
		},
		getQQOauth:function(){
			uni.showToast({
				title: '手机QQ暂时停止服务',
				position:'bottom',
				icon:'none',
				position:'center'
