		},
		getTokenCode:function(){
			uni.showLoading({
				title: '获取中'
			});
			if(this.code==true){
				if(!this.user){
					uni.showToast({
						title: '请输入手机号',
						position:'bottom',
						icon:'none',
						position:'center'
					});
					return;
				}
				this.codeTitle="59s"
				this.code=false
				this.timer = setInterval(() => {
				  //当num等于100时清除定时器
					this.num--;
					if (this.num == 0) {
						this.code=true
					    clearInterval(this.timer);
						this.num=58;
						this.codeTitle="获取验证码"
						
					}else{
						this.codeTitle=this.num+"s"
						this.code=false
					}
				}, 1000);
				uni.request({
					method:'POST',
					url: 'https://api.angeli.top/reg.php?type=getCode',//获取验证码
					data: {
						phone:this.user
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
						'Cookie':server.cookie
					},
					success: (res) => {
						console.log(res);
						if(res.data.code=="1"){
							//server.cookie=res.header['Set-Cookie'];
							console.log("获取验证码")
							uni.showToast({
								title: '验证码发送成功！',
								position:'bottom',
								icon:'none',
								position:'center'
							});
							
							console.log('获取验证码cookie',server.cookie);
						}else{
							uni.showToast({
								title: '验证码发送失败！',
								position:'bottom',
								icon:'none',
								position:'center'
							});
							clearInterval(this.timer);
							this.code=true
							clearInterval(this.timer);
							this.num=58;
							this.codeTitle="获取验证码"
						}
						
					},
					fail:function(){
						this.code=true
						clearInterval(this.timer);
						this.num=58;
						this.codeTitle="获取验证码"
						uni.showToast({
							title: '验证码获取失败！请尝试其他方式登录',
							position:'bottom',
							icon:'none',
							position:'center'
						});
					}
				});
			}else{
				console.log("非法获取验证码")
			}
			uni.hideLoading();
		},
		login:function(){
			if(!this.user || !this.password){
				uni.showToast({
					title: '请输入正确的手机号和验证码！',
					position:'bottom',
					icon:'none',
					position:'center'
				});
				return;
			}
			console.log('提交的cookie：',server.cookie);
			uni.request({
				method:'POST',
				url: 'https://api.angeli.top/reg.php?type=codeLogin', //仅为示例，并非真实接口地址。
				data: {
					phone: this.user,
					code:this.password
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					console.log(res);
					if(res.data.code=="1"){//登陆成功
						server.userinfo=res.data.data;
						server.cookie=res.header['Set-Cookie'];
						uni.setStorage({
							key: 'cookie',
							data: res.header['Set-Cookie'],
							success: function () {
								console.log("储存cookie成功！")
								uni.setStorage({
									key: 'userinfo',
									data: res.data.data,
									success: function () {
										uni.showToast({
											title: res.data.msg,
											position:'bottom',
											icon:'none',
											duration:2000,
											mask:true
										});
										
										if(res.data.code=="1"){
											setTimeout(function () {
												uni.redirectTo({
													url: '../Home/Home'
												})
											}, 1500);
										}
									}
								});
							}
						});
					}
					if(res.data.code=="2"){//新用户注册
						uni.navigateTo({
							url: '../newUser/newUser?phone='+res.data.data
						});
					}else{
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none',
							position:'center'
						});
					}
				}
			});
			
			
