		},
		appreg:function(){
			uni.showToast({
				title: '内测期间关闭APP注册通道',
				position:'bottom',
				icon:'none'
			});
		},
		inputphone:function(e){
			this.user=e.detail.value
			console.log(this.user);
		},
		inputpassword:function(e){
			this.password=e.detail.value;
			console.log(this.password);
		},
		hid:function(){
			this.modalName="";
			
		},
		phone:function(e){
			uni.request({
				method:'POST',
				url: 'https://api.angeli.top/WeChat/demo.php', //仅为示例，并非真实接口地址。
				data: {
					iv: e.detail.iv,
					data:e.detail.encryptedData,
					sk:server.usersk
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					this.phoneifo = res.data.phoneNumber;
					uni.request({
						method:'POST',
						url: 'https://api.angeli.top/reg.php?type=wxreg', //仅为示例，并非真实接口地址。
						data: {
							unionid: this.userInfo.unionId,
							username:this.userInfo.nickName,
							avatarUrl:this.userInfo.avatarUrl,
							gender:this.userInfo.gender,
							phone:this.phoneifo,
							openid:this.userInfo.openId,
							tuijianId:server.tgid
						},
						header: {
							'content-type': 'application/x-www-form-urlencoded',
							'Cookie':server.cookie
						},
						success: (res) => {
							console.log(res);
							if(res.data.code=="1"){
								uni.showToast({
									title: res.data.msg,
									position:'bottom'
								});
								setTimeout(function () {
									uni.redirectTo({
										url: '../Home/Home'
									})
								}, 2000);
							}else{
								uni.showModal({
									title: '注册失败！',
									content: '错误原因' + res.data,
									showCancel: false
								})
							}
						}
					});
					
				}
			});
		},
		mpGetUserInfo(result) {
			console.log(result)
			if (result.detail.errMsg !== 'getUserInfo:ok') {
				uni.showModal({
					title: '获取用户信息失败',
					content: '错误原因' + result.detail.errMsg,
					showCancel: false
				});
				return;
			}
			uni.request({
				method:'POST',
				url: 'https://api.angeli.top/WeChat/demo.php', //仅为示例，并非真实接口地址。
				data: {
					iv: result.detail.iv,
					data:result.detail.encryptedData,
					sk:server.usersk
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					if(res.data.openId){
						this.reg = true;
						this.disabled = false;
						this.userInfo = res.data;
						console.log('userinfo||', this.userInfo);
						this.modalName = 'Image'
						
					}else{
						uni.showToast({
							title: '错误：'+res.data,
							position:'bottom',
							icon:'none'
						});
					}
				}
			});
			
			
		}
	}
